'use strict';

const React = require('react');

class PlayPause extends React.Component {
  /**
   * Button for starting and stopping timer
   * @param {{timer:Timer}} props
   */
  constructor(props) {
    super(props);
    this.state = {play: false};
    this.clicked = this.clicked.bind(this);
    
    props.timer.on('running', isRunning => this.setState({play: isRunning}));
  }
  
  clicked() {
    const play = !this.state.play;
    const timer = this.props.timer;
    
    play ? timer.start() : timer.stop();
  }
  
  render() {
    const icon = this.state.play ? 'glyphicon-pause' : 'glyphicon-play';
    
    /*return <button className="btn btn-info" onClick={this.clicked}>
      <div className={`glyphicon ${icon}`}></div>
    </button>;*/
    
    return <div className={`glyphicon ${icon} waves-effect waves-light btn-flat`} onClick={this.clicked}>
      <i className="large material-icons">insert_chart</i>
    </div>;
  }
}

module.exports = PlayPause;


